import "./StopWatch.scss";

type StopWatchProps = {
  hours: number;
  minutes: number;
  seconds: number;
};

function StopWatch(props: StopWatchProps) {
  const timeString = [props.hours, props.minutes, props.seconds]
    .map((t) => t.toString().padStart(2, "0"))
    .join(":");

  return <div className="StopWatch">{timeString}</div>;
}

export default StopWatch;
